'use client'

import React, { useState, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import { getToken, isAuthenticated } from "@/lib/utils/auth";
import { getUserId } from "@/lib/utils/auth";
import { SidebarComponent } from "@/components/Sidebar/SidebarComponent";
import NotificationItem from "@/components/Notification/NotificationItem";
import { getAllNotifications, markNotificationAsRead, markAllNotificationsAsRead } from "@/lib/api/notificationAPI";
import { Notification } from "@/types/api.types";
import toast from "react-hot-toast";

export default function NotificationPage() {
    const [notifications, setNotifications] = useState<Notification[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [isMobile, setIsMobile] = useState(false);
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const router = useRouter();

    // Check authentication
    useEffect(() => {
        if (typeof window === 'undefined') return;
        if (!isAuthenticated()) {
            router.push('/login');
        }
    }, [router]);

    // Check mobile
    useEffect(() => {
        if (typeof window === 'undefined') return;
        const checkMobile = () => {
            setIsMobile(window.innerWidth < 768);
        };
        checkMobile();
        window.addEventListener('resize', checkMobile);
        return () => window.removeEventListener('resize', checkMobile);
    }, []);

    // Fetch notifications
    const fetchNotifications = useCallback(async () => {
        const userId = getUserId();
        if (!userId) return;

        try {
            setIsLoading(true);
            const data = await getAllNotifications(userId);
            const sorted = (data || []).sort((a: Notification, b: Notification) => {
                const timeA = a.createdDate ? new Date(a.createdDate).getTime() : 0;
                const timeB = b.createdDate ? new Date(b.createdDate).getTime() : 0;
                return timeB - timeA;
            });
            setNotifications(sorted);
        } catch (err) {
            console.error('Error fetching notifications:', err);
        } finally {
            setIsLoading(false);
        }
    }, []);

    useEffect(() => {
        const token = getToken();
        if (token) {
            fetchNotifications();
        }
    }, [fetchNotifications]);

    // Handle mark as read
    const handleMarkAsRead = async (notificationId: string) => {
        try {
            await markNotificationAsRead(notificationId);
            setNotifications(prev => prev.map(n =>
                n.id === notificationId ? { ...n, seen: true } : n
            ));
        } catch (err) {
            console.error('Error marking as read:', err);
        }
    };

    // Handle mark all as read
    const handleMarkAllAsRead = async () => {
        try {
            await markAllNotificationsAsRead();
            setNotifications(prev => prev.map(n => ({ ...n, seen: true })));
            toast.success('All notifications marked as read');
        } catch (err) {
            console.error('Error marking all as read:', err);
            toast.error('Could not mark notifications as read');
        }
    };

    // Handle notification click
    const handleNotificationClick = (notification: Notification) => {
        handleMarkAsRead(notification.id);
        if (notification.postId) {
            router.push(`/post/${notification.postId}`);
        }
    };

    const unreadCount = notifications.filter(n => !n.seen).length;

    return (
        <div className="flex min-h-screen">
            {/* Mobile menu button */}
            {isMobile && (
                <button
                    onClick={() => setSidebarOpen(!sidebarOpen)}
                    className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
                    aria-label="Toggle menu"
                >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {sidebarOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        )}
                    </svg>
                </button>
            )}

            {/* Mobile overlay */}
            {isMobile && sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar - Desktop */}
            <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
                <SidebarComponent />
            </div>

            {/* Sidebar - Mobile */}
            <div
                className={`
                    md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
                    transition-transform duration-300 ease-in-out
                    ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
                `}
            >
                <SidebarComponent />
            </div>

            {/* Main Content */}
            <div className={`flex-1 min-h-screen w-full max-w-full overflow-x-hidden ${isMobile ? 'pt-16 px-4' : 'pl-3'}`}>
                <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
                    <div className="max-w-2xl mx-auto px-4 py-8">
                        {/* Header */}
                        <div className="flex items-center justify-between mb-6">
                            <div>
                                <h1 className="text-2xl font-bold text-gray-800">Notifications</h1>
                                {unreadCount > 0 && (
                                    <p className="text-sm text-gray-500">{unreadCount} unread</p>
                                )}
                            </div>
                            {unreadCount > 0 && (
                                <button
                                    onClick={handleMarkAllAsRead}
                                    className="text-sm text-sky-600 hover:text-sky-700 font-medium"
                                >
                                    Mark all as read
                                </button>
                            )}
                        </div>

                        {/* Notifications List - Using Component */}
                        <div className="bg-white rounded-2xl shadow-sm border border-gray-100 overflow-hidden">
                            {isLoading ? (
                                <div className="divide-y divide-gray-100">
                                    {Array.from({ length: 5 }).map((_, i) => (
                                        <div key={i} className="p-4 animate-pulse">
                                            <div className="flex items-center gap-3">
                                                <div className="w-12 h-12 bg-gray-200 rounded-full" />
                                                <div className="flex-1 space-y-2">
                                                    <div className="h-4 bg-gray-200 rounded w-3/4" />
                                                    <div className="h-3 bg-gray-100 rounded w-1/2" />
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            ) : notifications.length > 0 ? (
                                <div className="divide-y divide-gray-100">
                                    {notifications.map((notification) => (
                                        <NotificationItem
                                            key={notification.id}
                                            notification={notification}
                                            onMarkRead={handleMarkAsRead}
                                            onClick={handleNotificationClick}
                                        />
                                    ))}
                                </div>
                            ) : (
                                <div className="p-12 text-center">
                                    <div className="w-24 h-24 mx-auto mb-6 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
                                        <svg className="w-12 h-12 text-sky-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                                                d="M15 17h5l-1.405-1.405A2.032 2.032 0 0118 14.158V11a6.002 6.002 0 00-4-5.659V5a2 2 0 10-4 0v.341C7.67 6.165 6 8.388 6 11v3.159c0 .538-.214 1.054-.595 1.435L4 17h5m6 0v1a3 3 0 11-6 0v-1m6 0H9" />
                                        </svg>
                                    </div>
                                    <h3 className="text-xl font-bold text-gray-800 mb-2">No notifications</h3>
                                    <p className="text-gray-500 text-sm">You&apos;re all caught up!</p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
