'use client'

import React from 'react';
import { Heart, MessageCircle, UserPlus, AtSign, Share2, Bell } from 'lucide-react';

interface NotificationIconProps {
    type: string;
}

const NotificationIcon: React.FC<NotificationIconProps> = ({ type }) => {
    const iconMap: { [key: string]: { icon: React.ReactNode; bgColor: string } } = {
        like: {
            icon: <Heart size={12} className="text-white fill-white" />,
            bgColor: 'bg-gradient-to-br from-red-500 to-pink-500',
        },
        comment: {
            icon: <MessageCircle size={12} className="text-white" />,
            bgColor: 'bg-gradient-to-br from-blue-500 to-indigo-500',
        },
        follow: {
            icon: <UserPlus size={12} className="text-white" />,
            bgColor: 'bg-gradient-to-br from-green-500 to-emerald-500',
        },
        mention: {
            icon: <AtSign size={12} className="text-white" />,
            bgColor: 'bg-gradient-to-br from-purple-500 to-violet-500',
        },
        message: {
            icon: <MessageCircle size={12} className="text-white fill-white" />,
            bgColor: 'bg-gradient-to-br from-sky-500 to-blue-500',
        },
        share: {
            icon: <Share2 size={12} className="text-white" />,
            bgColor: 'bg-gradient-to-br from-orange-500 to-amber-500',
        },
        default: {
            icon: <Bell size={12} className="text-white" />,
            bgColor: 'bg-gradient-to-br from-gray-500 to-gray-600',
        },
    };

    const config = iconMap[type] || iconMap.default;

    return (
        <div className={`w-5 h-5 rounded-full flex items-center justify-center ${config.bgColor} shadow-sm border-2 border-white`}>
            {config.icon}
        </div>
    );
};

export default NotificationIcon;
